@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
.nav {
  display: flex;
  justify-content: space-between;
  width: 100vw;
  margin: auto;
  font-family: "Goldman", cursive;
  padding-left: 2vw;
}
.logo {
  width: 70px;
  height: 70px;
  position: relative;
  top: 15px;
  left: 100px;
  cursor: pointer;
}
.navbar {
  flex: 2 1;
  display: flex;
  list-style: none;
  position: relative;
  left: 100px;
}
.navbar li {
  cursor: pointer;
  padding: 0 2vw;
  height: 100px;
  font-size: 1.3vw;
  line-height: 100px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.navbar li:hover {
  border-bottom: 5px solid rgb(56, 94, 217);
}
.navbar a {
  text-decoration: none;
}
.loginBt {
  position: relative;
  right: 100px;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  width: 8vw;
  height: 100px;
  font-size: 1.3vw;
  line-height: 100px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.loginBt:hover {
  border-bottom: 5px solid rgb(56, 94, 217);
}
.loginSuccess {
  width: 60px;
  height: 100px;
  position: relative;
  right: 100px;
}
.loginSuccessImg {
  position: relative;
  top: 25px;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: 2px solid silver;
}
.logoutBt {
  position: relative;
  right: 100px;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  width: 8vw;
  height: 100px;
  font-size: 1.3vw;
  line-height: 100px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.logoutBt:hover {
  border-bottom: 5px solid rgb(56, 94, 217);
}

.screen {
  max-width: 100vw;
  height: 100vh;
  background: center/cover no-repeat
    url("https://h7f7z2r7.stackpathcdn.com/sites/default/files/images/articles/geekrobot.jpg");
}






* {
  margin: 0;
  box-sizing: border-box;
}

